<!-- src/components/TopBar.vue -->
<template>
  <div class="top-bar" :class="{ 'hidden': isHidden && !isSidebarOpen && !isTocOpen }">
    <div class="top-bar-content">
      <button class="menu-button" @click="toggleSidebar">
        <font-awesome-icon :icon="['fas', isSidebarOpen ? 'times' : 'bars']" />
      </button>
      <h1 class="page-title" :class="{ 'visible': showTitleInTopBar }">{{ pageTitle }}</h1>
      <button v-if="isBlogPost" class="toc-button" @click="toggleToc">
        <font-awesome-icon :icon="['fas', isTocOpen ? 'times' : 'list']" />
      </button>
    </div>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { computed, ref, onMounted, onUnmounted } from 'vue'
import { useRoute } from 'vue-router'

export default {
  name: 'TopBar',
  setup() {
    const store = useStore()
    const route = useRoute()

    const isHidden = ref(false)
    const isSidebarOpen = computed(() => store.state.content.sidebarOpen)
    const isTocOpen = computed(() => store.state.content.tocSidebarOpen)
    const isBlogPost = computed(() => route.name === 'BlogPost')

    let lastScrollTop = 0
    const scrollThreshold = 5
    let hideTimeout = null
    const hideDelay = 3000 // 3 seconds

    const setHideTimeout = () => {
      clearTimeout(hideTimeout)
      hideTimeout = setTimeout(() => {
        if (!isSidebarOpen.value && !isTocOpen.value) {
          isHidden.value = true
          store.commit('content/setTopBarHidden', true)
        }
      }, hideDelay)
    }

    const handleScroll = () => {
      const st = window.pageYOffset || document.documentElement.scrollTop
      if (Math.abs(lastScrollTop - st) <= scrollThreshold) return
      isHidden.value = st > lastScrollTop && st > 50
      if (st <= 0 || st < lastScrollTop) {
        isHidden.value = false
        setHideTimeout()
      }
      lastScrollTop = st <= 0 ? 0 : st
      store.commit('content/setTopBarHidden', isHidden.value)
    }

    const handleMouseMove = () => {
      if (isHidden.value) {
        isHidden.value = false
        store.commit('content/setTopBarHidden', false)
      }
      setHideTimeout()
    }

    onMounted(() => {
      window.addEventListener('scroll', handleScroll, { passive: true })
      window.addEventListener('mousemove', handleMouseMove)
      setHideTimeout()
    })

    onUnmounted(() => {
      window.removeEventListener('scroll', handleScroll)
      window.removeEventListener('mousemove', handleMouseMove)
      clearTimeout(hideTimeout)
    })

    const toggleSidebar = () => {
      store.dispatch('content/toggleSidebar')
      setHideTimeout()
    }

    const toggleToc = () => {
      store.dispatch('content/toggleTocSidebar')
      setHideTimeout()
    }

    const pageTitle = computed(() => {
      if (route.name === 'BlogPost') {
        return store.state.content.currentBlogPostTitle || 'Blog Post'
      }
      return route.meta.title || ''
    })

    const showTitleInTopBar = computed(() => store.state.content.showTitleInTopBar)

    return {
      isHidden,
      isSidebarOpen,
      isTocOpen,
      toggleSidebar,
      toggleToc,
      pageTitle,
      showTitleInTopBar,
      isBlogPost
    }
  }
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--topbar-background);
  height: var(--topbar-height);
  transition: transform 0.3s ease-in-out;
  z-index: var(--z-index-topbar);
}
.top-bar.hidden {
  transform: translateY(-100%);
}

.top-bar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 var(--spacing-medium);
}

.menu-button, .toc-button {
  background: none;
  border: none;
  font-size: 1.2rem;
  color: var(--text-color);
  cursor: pointer;
}

.page-title {
  margin: 0;
  font-size: 1.2rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.page-title.visible {
  opacity: 1;
}
</style>
